v-84b5a07c]{background:#461952;background-image:url(/assets/login/Group-1821.png);background-position:0 100%;background-repeat:no-repeat;border-radius:16px;padding:55px;width:50%}#login-splide-cont[data-v-84b5a07c],.image-view-cont[data-v-84b5a07c]{height:100%;width:100%}.image-view-cont[data-v-84b5a07c]{-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);background:hsla(0,0%,91%,.4);background-position-x:right;background-position-y:bottom;background-repeat:no-repeat;background-size:65%;border:1px solid #fff;border-radius:15px;box-shadow:5px 5px 4px hsla(0,1%,40%,.25);padding:32px 0 0 32px}.image-view-cont>p[data-v-84b5a07c]{color:#fff;font-family:Poppins;font-size:30.6966px;font-style:normal;font-weight:500;line-height:46px;width:60%}.forn-grp-dbl input[data-v-84b5a07c]{margin-top:0}", "",{"version":3,"sources":["webpack://./resources/js/components/auth/Login.vue"],"names":[],"mappings":"AA+rBA,yBACA,SAAA,CACA,cACA,CACA,kCACA,qBAAA,CAEA,YAAA,CACA,eAAA,CAFA,YAGA,CACA,kCAIA,kBAAA,CAFA,YAAA,CACA,sBAAA,CAFA,SAIA,CACA,kCAEA,YAAA,CACA,qBAAA,CACA,WAAA,CAHA,SAIA,CACA,oCACA,UACA,CACA,qCAMA,aAAA,CALA,iBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAEA,iBACA,CACA,sCAIA,mBAAA,CADA,eAAA,CAEA,gBAAA,CAGA,wBACA,CACA,0EAJA,UAAA,CALA,mBAAA,CACA,iBAAA,CAKA,kBAWA,CARA,oCAIA,mBAAA,CADA,eAAA,CAEA,gBAGA,CACA,mCAMA,aAAA,CACA,YAAA,CACA,qBAAA,CAPA,iBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAIA,kBACA,CACA,yCACA,yBAAA,CACA,wCAAA,CAEA,iCAAA,CADA,iDAAA,CAGA,cAAA,CADA,WAEA,CACA,uCAGA,mBAAA,CADA,WAAA,CAGA,iBAAA,CADA,gBAAA,CAHA,UAKA,CACA,wCAMA,aAAA,CACA,oBAAA,CANA,iBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAGA,gBACA,CACA,+BACA,YAAA,CACA,6BAAA,CACA,aACA,CACA,8BAQA,kBAAA,CAEA,aAAA,CAHA,YAAA,CAEA,gBAAA,CARA,oBAUA,CACA,8HAVA,iBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAqBA,CAfA,gGAaA,UAEA,CACA,sIAPA,kBAAA,CAPA,WAAA,CAQA,iBAAA,CACA,UAAA,CAGA,kBAAA,CAXA,YAAA,CASA,YAkBA,CAdA,sCAaA,cAAA,CAVA,iBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAOA,CACA,6CACA,kBACA,CACA,oCACA,YAAA,CACA,6BAAA,CACA,aACA,CACA,8EAcA,kBAAA,CAVA,eAAA,CACA,8BAAA,CAEA,uBAAA,CADA,uCAAA,CAUA,aAAA,CAIA,cAAA,CAPA,YAAA,CALA,iBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CASA,sBAAA,CAPA,gBAAA,CATA,YAAA,CAYA,gBAAA,CAGA,iBAAA,CAhBA,oBAAA,CAeA,SAIA,CACA,wCACA,UACA,CACA,oFAIA,WAAA,CAFA,gBAAA,CACA,UAEA,CACA,uDACA,iBACA,CACA,kCAOA,aAAA,CANA,mBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CACA,iBAEA,CACA,oCAEA,aAAA,CADA,oBAEA,CACA,oCAEA,uBAAA,CAGA,YAAA,CACA,cAAA,CAHA,eAIA,CACA,sCAGA,eAAA,CAUA,wBAAA,CATA,uBAAA,CAOA,UAAA,CACA,cAAA,CANA,mBAAA,CAGA,kBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAIA,gBAAA,CATA,gBAAA,CAJA,oBAcA,CACA,kCAEA,kBAAA,CAEA,kDAAA,CACA,0BAAA,CACA,2BAAA,CAHA,kBAAA,CAIA,YAAA,CANA,SAOA,CAKA,sEAFA,WAAA,CADA,UAgBA,CAbA,kCAQA,kCAAA,CAAA,0BAAA,CAPA,4BAAA,CAEA,2BAAA,CACA,4BAAA,CAFA,2BAAA,CAGA,mBAAA,CACA,qBAAA,CAGA,kBAAA,CAFA,yCAAA,CAGA,qBAGA,CACA,oCAOA,UAAA,CANA,mBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CACA,SAEA,CACA,qCACA,YACA","sourcesContent":["<template>\n    <div class=\"login-main-cont\">\n        <div class=\"login-left-cont\">\n            <div class=\"login-form-cont\">\n                <h1>Welcome to Happywed!</h1>\n                <p class=\"hide-mobile\" v-if=\"url === 'host'\">\n                    Wedding Organizers! you have access to a range of tools that\n                    make managing your wedding events easier. Login now to\n                    create and manage your wedding invitations, track RSVPs, and\n                    communicate with your guests.\n                </p>\n                <p class=\"hide-mobile\" v-else-if=\"url === 'vendor'\">\n                    Vendors! As a member of our community, you have access to\n                    our platform to showcase your professional services to\n                    couples seeking your expertise. Simply log in to your\n                    account to manage your business and connect with potential\n                    clients.\n                </p>\n                <p class=\"hide-mobile\" v-else-if=\"url === 'guest'\">\n                    Guests! you can log in to your account to view your wedding\n                    invitation, RSVP, and provide your family member's details,\n                    as well as your arrival and departure details. Log in now to\n                    ensure your attendance is counted and you don't miss out on\n                    any important wedding details.\n                </p>\n                <p v-else>Welcome! Wedding Community Members</p>\n\n                <!-- login with password -->\n                <form\n                    @submit.prevent\n                    v-if=\"loginWithOtp == false\"\n                    method=\"POST\"\n                >\n                    <input type=\"hidden\" name=\"_token\" v-bind:value=\"csrf\" />\n                    <label for=\"email\" class=\"login-form-group\">\n                        E-Mail Address/ Mobile\n                        <input\n                            type=\"text\"\n                            name=\"email\"\n                            id=\"email\"\n                            v-model=\"email\"\n                            @change=\"emptyData()\"\n                            @input=\"emptyData()\"\n                        />\n                        <span\n                            v-if=\"errorSubmit && errorSubmit.email\"\n                            class=\"errMsg\"\n                            >{{ errorSubmit.email[0] }}</span\n                        >\n                    </label>\n\n                    <div\n                        v-if=\"multiUsersExist == true\"\n                        class=\"multiple-users\"\n                        style=\"display: flex; justify-content: space-between\"\n                    >\n                        <span\n                            @click=\"\n                                selectedUser = item;\n                                active = item;\n                            \"\n                            :class=\"selectedUser == item ? 'active' : ''\"\n                            v-for=\"(item, index) in multiUsers\"\n                            :key=\"index\"\n                        >\n                            {{ item }}\n                        </span>\n                    </div>\n                    <div\n                        v-if=\"\n                            (userExists && multiUsersExist == false) ||\n                            selectedUser != null\n                        \"\n                    >\n                        <label for=\"password\" class=\"login-form-group\">\n                            Password\n                            <input\n                                :type=\"showPass\"\n                                name=\"password\"\n                                id=\"pass\"\n                                v-model=\"password\"\n                            />\n                            <img\n                                @click=\"showPass = 'text'\"\n                                v-if=\"showPass == 'password'\"\n                                src=\"/assets/login/eye.svg\"\n                                alt=\"\"\n                            />\n                            <img\n                                @click=\"showPass = 'password'\"\n                                v-if=\"showPass == 'text'\"\n                                src=\"/assets/login/eye-off.svg\"\n                                alt=\"\"\n                            />\n                            <span\n                                v-if=\"errorSubmit && errorSubmit.password\"\n                                class=\"errMsg\"\n                                >{{ errorSubmit.password[0] }}</span\n                            >\n                        </label>\n\n                        <div class=\"forn-grp-dbl\">\n                            <label\n                                for=\"remember_token\"\n                                class=\"login-form-group\"\n                                style=\"flex-direction: row\"\n                            >\n                                <input\n                                    type=\"checkbox\"\n                                    name=\"remember_token\"\n                                    id=\"remember_token\"\n                                    value=\"true\"\n                                    v-model=\"remember_token\"\n                                />\n                                <span>Keep me logged in</span>\n                            </label>\n                            <a\n                                class=\"forgot-link\"\n                                :href=\"route(url + 'password.request')\"\n                                >Forgot Password?</a\n                            >\n                        </div>\n                    </div>\n                    <span v-if=\"errMsg\" class=\"errMsg\">{{ errMsg }}</span>\n                    <button\n                        v-if=\"selectedUser == null\"\n                        @click=\"checkUserExists()\"\n                        type=\"button\"\n                        :disabled=\"disableLogIn\"\n                    >\n                        Continue\n                    </button>\n                    <button\n                        v-if=\"selectedUser != null\"\n                        @click=\"loginUser\"\n                        type=\"button\"\n                        :disabled=\"disableLogIn\"\n                    >\n                        Log In\n                    </button>\n                </form>\n                <!-- login with otp -->\n                <form v-if=\"loginWithOtp == true\" @submit.prevent=\"verifyOtp\">\n                    <!-- <input type=\"hidden\" name=\"_token\" v-bind:value=\"csrf\" /> -->\n                    <label for=\"email\" class=\"login-form-group\">\n                        Mobile\n                        <input\n                            type=\"text\"\n                            name=\"mobile\"\n                            id=\"mobile\"\n                            v-model=\"email\"\n                        />\n                        <span\n                            v-if=\"asyncErrorSubmit && asyncErrorSubmit.mobile\"\n                            class=\"errMsg\"\n                            >{{ asyncErrorSubmit.mobile[0] }}</span\n                        >\n                    </label>\n\n                    <div\n                        v-if=\"multiUsersExist == true\"\n                        class=\"multiple-users\"\n                        style=\"display: flex; justify-content: space-between\"\n                    >\n                        <span\n                            @click=\"\n                                selectedUser = item;\n                                active = item;\n                            \"\n                            v-for=\"(item, index) in multiUsers\"\n                            :class=\"selectedUser == item ? 'active' : ''\"\n                            :key=\"index\"\n                        >\n                            {{ item }}\n                        </span>\n                    </div>\n\n                    <label\n                        v-if=\"otpSent == true\"\n                        for=\"otp\"\n                        class=\"login-form-group\"\n                    >\n                        OTP\n                        <input type=\"text\" name=\"otp\" id=\"otp\" v-model=\"otp\" />\n                        <span\n                            v-if=\"errorSubmit && errorSubmit.otp\"\n                            class=\"errMsg\"\n                            >{{ errorSubmit.otp[0] }}</span\n                        >\n                    </label>\n                    <button\n                        @click=\"sendOtp\"\n                        v-if=\"otpSent == false\"\n                        type=\"button\"\n                        :disabled=\"disableLogIn\"\n                    >\n                        Send Otp\n                    </button>\n                    <div v-else>\n                        <button type=\"submit\" :disabled=\"disableLogIn\">\n                            Log In\n                        </button>\n                        <button\n                            :disabled=\"disableLogIn\"\n                            @click=\"retryOtp\"\n                            type=\"button\"\n                        >\n                            Resend OTP\n                        </button>\n                    </div>\n                </form>\n\n                <!-- \"social-login-cont\" was earlier class applied and need to removed  -->\n                <div\n                    class=\"d-flex justify-content-center\"\n                    v-if=\"url == 'host' || url == 'vendor' || url == 'admin'\"\n                >\n                    <!-- <i class=\"text-small\">login with </i> -->\n                    <a class=\"mx-3\" :href=\"route('googleLogin')\">\n                        <img\n                            src=\"/assets/login/google.svg\"\n                            alt=\"\"\n                            style=\"margin-right: 4px\"\n                        />\n                        <!-- Log in with Google -->\n                    </a>\n                    <a class=\"mx-3\" :href=\"route('fblogin')\">\n                        <img\n                            src=\"/assets/login/logos_facebook.svg\"\n                            alt=\"\"\n                            style=\"margin-right: 4px\"\n                        />\n                        <!-- Log in with Facebook-->\n                    </a>\n                </div>\n                <!-- login with OTP button -->\n                <div\n                    v-if=\"\n                        (userExists &&\n                            multiUsersExist == false &&\n                            loginWithOtp != true &&\n                            isNumericString(email)) ||\n                        (selectedUser != null &&\n                            isNumericString(email) &&\n                            loginWithOtp != true)\n                    \"\n                    class=\"social-login-cont\"\n                >\n                    <div @click=\"loginWithOtp = true\">\n                        <i class=\"fas fa-sms\"></i>\n                        Log in with OTP\n                    </div>\n                </div>\n                <!-- login with password button -->\n                <div\n                    v-if=\"\n                        ((userExists &&\n                            multiUsersExist == false &&\n                            loginWithOtp == true) ||\n                        (selectedUser != null &&\n                            !isNumericString(email) &&\n                            loginWithOtp == true) ||\n                        loginWithOtp == true) && url != 'guest'\n                    \"\n                    class=\"social-login-cont\"\n                >\n                    <div @click=\"loginWithOtp = false\">\n                        <i class=\"fas fa-sms\"></i>\n                        Log in with Password\n                    </div>\n                </div>\n                <div v-if=\"url == 'host'\" class=\"sinup-link-cont\">\n                    Not a member? <a :href=\"route('hostRegister')\">Sign up</a>\n                </div>\n                <div v-else-if=\"url == 'vendor'\" class=\"sinup-link-cont\">\n                    Not a member? <a :href=\"route('vendorRegister')\">Sign up</a>\n                </div>\n                <div v-else-if=\"url == 'admin'\" class=\"sinup-link-cont\">\n                    Not a member? <a :href=\"route('adminRegister')\">Sign up</a>\n                </div>\n                <div v-else class=\"sinup-link-cont\"></div>\n                <!-- <div class=\"login-toggle-cont\">\n                    <a :href=\"route('hostLogin')\" v-if=\"active != 'host'\">\n                        Host\n                    </a>\n                    <a :href=\"route('login')\" v-if=\"active != 'guest'\">\n                        Guest\n                    </a>\n                    <a :href=\"route('brideLogin')\" v-if=\"active != 'bride'\">\n                        Bride\n                    </a>\n                    <a :href=\"route('groomLogin')\" v-if=\"active != 'groom'\">\n                        Groom\n                    </a>\n                    <a :href=\"route('vendorLogin')\" v-if=\"active != 'vendor'\">\n                        Vendor\n                    </a>\n                    <a :href=\"route('companyLogin')\" v-if=\"active != 'company'\">\n                        Planner\n                    </a>\n                    <a :href=\"route('memberLogin')\" v-if=\"active != 'member'\">\n                        Member\n                    </a>\n                </div> -->\n            </div>\n        </div>\n        <div class=\"login-pics-cont\">\n            <div class=\"splide\" id=\"login-splide-cont\">\n                <div class=\"splide__track\">\n                    <ul class=\"splide__list\">\n                        <li class=\"splide__slide\">\n                            <div\n                                class=\"image-view-cont\"\n                                style=\"\n                                    background-image: url('/assets/login/img1.png');\n                                \"\n                            >\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\n                                <p>\n                                    The easiest way to get from 'Save the Date'\n                                    to 'thanks so much'. Designed and customized\n                                    by you.\n                                </p>\n                            </div>\n                        </li>\n                        <li class=\"splide__slide\">\n                            <div\n                                class=\"image-view-cont\"\n                                style=\"\n                                    background-image: url('/assets/login/img7.png');\n                                \"\n                            >\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\n                                <p>\n                                    The online RSVP that's so simple and excited\n                                    for you & your guests.\n                                </p>\n                            </div>\n                        </li>\n                        <li class=\"splide__slide\">\n                            <div\n                                class=\"image-view-cont\"\n                                style=\"\n                                    background-image: url('/assets/login/img6.png');\n                                \"\n                            >\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\n                                <p>\n                                    Decide who to celebrate with when and where,\n                                    we'll help the rest-from collecting contact\n                                    info to managing RSVPs.\n                                </p>\n                            </div>\n                        </li>\n                        <li class=\"splide__slide\">\n                            <div\n                                class=\"image-view-cont\"\n                                style=\"\n                                    background-image: url('/assets/login/img5.png');\n                                \"\n                            >\n                                <img src=\"/assets/login/logo.png\" alt=\"\" />\n                                <p>\n                                    Find pre-screened vendors and get\n                                    personalised recs. Reach out here and book\n                                    when you're ready.\n                                </p>\n                            </div>\n                        </li>\n                    </ul>\n                </div>\n            </div>\n        </div>\n        <FlashMessage :message=\"message\"></FlashMessage>\n    </div>\n</template>\n\n<script>\nimport FlashMessage from \"@/components/FlashMessage.vue\";\n\nexport default {\n    props: [\"errorSubmit\", \"oldvalues\", \"url\"],\n    components: {\n        FlashMessage,\n    },\n    data() {\n        return {\n            splider: null,\n            active: null,\n            disableLogIn: false,\n            email: this.oldvalues[\"email\"] ?? null,\n            mobile: this.oldvalues[\"mobile\"] ?? null,\n            password: this.oldvalues[\"password\"] ?? null,\n            otp: this.oldvalues[\"otp\"] ?? null,\n            remember_token: this.oldvalues[\"remember_token\"] ?? false,\n            //formRoute: route(\"hostLogin\"),\n            csrf: $('meta[name=\"csrf-token\"]').attr(\"content\"),\n            showPass: \"password\",\n            loginWithOtp: this.url == 'guest' ? true :false,\n            otpSent: false,\n            message: null,\n            asyncErrorSubmit: null,\n            userExists:\n                this.url === \"guest\"\n                    ? true\n                    : this.url === \"admin\"\n                    ? true\n                    : this.url === \"vendor\"\n                    ? true\n                    : false,\n            multiUsersExist: false,\n            multiUsers: [],\n            activeUser: null,\n            selectedUser:\n                this.url === \"guest\"\n                    ? \"guest\"\n                    : this.url === \"admin\"\n                    ? \"admin\"\n                    : this.url === \"vendor\"\n                    ? \"vendor\"\n                    : null,\n            errMsg: null,\n        };\n    },\n    methods: {\n        isNumericString(str) {\n            return !isNaN(str) && !isNaN(parseFloat(str));\n        },\n        sendOtp() {\n            let _this = this;\n            _this.disableLogIn = true;\n            let formData = new FormData();\n            let link = route(\"sendOtp\");\n            formData.append(\"mobile\", _this.email);\n            formData.append(\"role\", _this.selectedUser);\n\n            axios({\n                url: link,\n                method: \"POST\",\n                data: formData,\n            })\n                .then((response) => {\n                    _this.disableLogIn = false;\n                    console.log(response.data);\n                    if (response.data[\"type\"] == \"success\") {\n                        _this.otpSent = true;\n                    }\n                    _this.message = response.data[\"message\"];\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    _this.disableLogIn = false;\n                    _this.asyncErrorSubmit = error.response.data.errors;\n                    _this.otpSent = false;\n                    if (error.response.status == 429) {\n                        _this.message =\n                            \"Too many attempt! Please try again later\";\n                        setTimeout(function () {\n                            _this.message = null;\n                        }, 3000);\n                    }\n                });\n        },\n        retryOtp() {\n            let _this = this;\n            _this.disableLogIn = true;\n            let formData = new FormData();\n            let link = route(\"retryOtp\");\n            formData.append(\"mobile\", _this.email);\n            axios({\n                url: link,\n                method: \"POST\",\n                data: formData,\n            })\n                .then((respons